<style lang="less">
@import '../../styles/common.less';
@import 'components/table.less';
.demo-spin-icon-load {
    animation: ani-demo-spin 1s linear infinite;
}
</style>
<template>
<div>
  <Row>
    <Col span="6">
    <Card>
      <p slot="title">
        <Icon type="ios-redo"></Icon>
        
      </p>
      <div class="edittable-test-con">
        <Form :model="formItem" :label-width="100" ref="formItem" :rules="ruleValidate">
          <Form-item label=":" prop="computer_room">
            <Select v-model="formItem.computer_room" placeholder="" @on-change="Connection_Name">
                <Option v-for="i in dataset" :value="i" :key="i">{{ i }}</Option>
            </Select>
          </Form-item>
          <Form-item label=":" prop="connection_name">
            <Select v-model="formItem.connection_name" placeholder="" @on-change="DataBaseName">
              <Option v-for="i in tableform.sqlname" :value="i.connection_name" :key="i.connection_name" filterable>{{ i.connection_name }}</Option>
            </Select>
          </Form-item>
          <Form-item label=":" prop="basename">
            <Select v-model="formItem.basename" placeholder="" @on-change="GetTableName" filterable>
              <Option v-for="item in tableform.basename" :value="item" :key="item">{{ item }}</Option>
            </Select>
          </Form-item>
          <Form-item label=":">
            <Select v-model="formItem.tablename" placeholder="" filterable>
              <Option v-for="item in tableform.info" :value="item" :key="item">{{ item }}</Option>
            </Select>
          </Form-item>
          <Button type="warning" @click="canel()" style="margin-left: 15%"></Button>
          <Button type="primary" @click="getinfo()" style="margin-left: 3%"></Button>
          <Button type="success" @click="confirmsql()" style="margin-left: 3%"></Button>
        </Form>
        <br>
        <Tabs value="order1" style="height: 300px;overflow-y: scroll;">
          <TabPane label="DDL" name="order1">
            <p v-for="list in sql" style="font-size: 12px;color:#2b85e4"> {{ list }}<br><br></p>
          </TabPane>
          <TabPane label="" name="order2">
            <Button type="primary" style="margin-left: 25%;margin-top: 20%;" @click.native="orderswitch" size="large"></Button>
          </TabPane>
        </Tabs>
      </div>
    </Card>
    </Col>
    <Col span="18" class="padding-left-10">
    <Card>
      <p slot="title">
        <Icon type="android-remove"></Icon>
        
      </p>
      <div class="edittable-table-height-con">
        <Tabs :value="tabs">
          <TabPane label="" name="order1" icon="edit">
            <Form>
              <FormItem>
                <editor v-model="formDynamic" @init="editorInit"></editor>
              </FormItem>
              <FormItem>
                <Table :columns="columnsName" :data="Testresults" highlight-row></Table>
              </FormItem>
              <FormItem>
                <Button type="warning" @click="test_sql"></Button>
                <Button type="primary" @click="handleSubmit(formDynamic)" style="margin-left: 3%" :disabled="this.validate_gen">DDL</Button>
              </FormItem>
            </Form>
          </TabPane>
          <TabPane label="" name="order3" icon="plus">
            <Table stripe :columns="addcolums" :data="add_row" height="385" border></Table>
            <div style="margin-top: 5%">
              <Input v-model="Add_tmp.Field" placeholder="" style="width: 10%"></Input>
              <Select v-model="Add_tmp.Species" style="width: 15%" transfer placeholder="">
                <Option v-for="i in optionData" :key="i" :value="i">{{i}}</Option>
              </Select>
              <Input v-model="Add_tmp.Len" placeholder="" style="width: 10%"></Input>
              <Select v-model="Add_tmp.Null" style="width: 15%" placeholder="" transfer>
                      <Option value="YES">YES</Option>
                      <Option value="NO">NO</Option>
              </Select>
              <Input v-model="Add_tmp.Default" placeholder="" style="width: 15%"></Input>
              <Input v-model="Add_tmp.Extra" placeholder="" style="width: 15%"></Input>
              <Button type="warning" @click.native="ClearColumns"></Button>
              <Button type="info" @click.native="AddColumns()"></Button>
            </div>
          </TabPane>
          <TabPane label="&" name="order4" icon="edit">
            <edittable refs="table2" v-model="TableDataNew" :columns-list="tabcolumns" @index="remove"></edittable>
          </TabPane>
        </Tabs>
      </div>
    </Card>
    </Col>
  </Row>

  <Modal v-model="openswitch" @on-ok="commitorder" :ok-text="''" width="800">
    <Row>
      <Card>
        <div class="step-header-con">
          <h3 style="margin-left: 35%">OPS</h3>
        </div>
        <p class="step-content"></p>
        <Form class="step-form" :label-width="100">
          <FormItem label=":">
            <p>{{username}}</p>
          </FormItem>
          <FormItem label=":">
            <p>{{formItem.basename}}</p>
          </FormItem>
          <FormItem label=":">
            <p>{{formItem.tablename}}</p>
          </FormItem>
          <FormItem label="SQL:">
            <p v-for="i in sql">{{i}}</p>
          </FormItem>
          <FormItem label=":" required>
            <Input v-model="formItem.text" placeholder="20"></Input>
          </FormItem>
          <FormItem label=":" required>
            <Select v-model="formItem.assigned" filterable transfer>
              <Option v-for="i in assigned" :value="i" :key="i">{{i}}</Option>
            </Select>
          </FormItem>
          <FormItem label="">
            <RadioGroup v-model="formItem.backup">
              <Radio label="1"></Radio>
              <Radio label="0"></Radio>
            </RadioGroup>
          </FormItem>
          <FormItem label="" required>
            <Checkbox v-model="pass"></Checkbox>
          </FormItem>
        </Form>
      </Card>
    </Row>
  </Modal>
</div>
</template>

<script>
import Cookies from 'js-cookie'
import axios from 'axios'
import util from '../../libs/util'
import edittable from './components/editTable'
import ICol from 'iview/src/components/grid/col'
export default {
  components: {
    ICol,
    edittable,
    editor: require('../../libs/editor')
  },
  data () {
    return {
      dataset: util.computer_room,
      item: {},
      basename: [],
      sqlname: [],
      TableDataOld: [],
      TableDataNew: [],
      tableform: {
        sqlname: [],
        basename: [],
        info: []
      },
      columnsName: [
        {
          title: 'ID',
          key: 'ID',
          width: '50'
        },
        {
          title: '',
          key: 'stage',
          width: '100'
        },
        {
          title: '',
          key: 'errlevel',
          width: '100'
        },
        {
          title: '',
          key: 'stagestatus',
          width: '150'
        },
        {
          title: '',
          key: 'errormessage'
        },
        {
          title: 'sql',
          key: 'sql'
        },
        {
          title: 'SQL',
          key: 'affected_rows',
          width: '130'
        }
      ],
      Testresults: [],
      tabcolumns: [
        {
          title: '',
          key: 'Field'
        },
        {
          title: '',
          key: 'Type',
          editable: true
        },
        {
          title: '',
          key: 'Null',
          editable: true,
          option: true
        },
        {
          title: '',
          key: 'Default',
          editable: true
        },
        {
          title: '',
          key: 'Key'
        },
        {
          title: '',
          key: 'Extra'
        },
        {
          title: '',
          align: 'center',
          width: 190,
          key: 'handle',
          handle: ['edit', 'delete']
        }
      ],
      putdata: [],
      Add_tmp: {
        Field: '',
        Type: '',
        Null: null,
        Default: null,
        Extra: null,
        Len: '',
        Species: null
      },
      add_row: [],
      username: Cookies.get('user'),
      addcolums: [
        {
          title: '',
          key: 'Field'
        },
        {
          title: '',
          key: 'Type'
        },
        {
          title: '',
          key: 'Null'
        },
        {
          title: '',
          key: 'Default'
        },
        {
          title: '',
          key: 'Extra'
        },
        {
          title: 'action',
          width: 80,
          render: (h, params) => {
            return h('Button', {
              props: {
                type: 'text'
              },
              on: {
                click: () => {
                  this.$Notice.error({
                    title: `${this.add_row[params.index].Field}-!`
                  })
                  this.add_row.splice(params.index, 1)
                }
              }
            }, '')
          }
        }
      ],
      sql: [],
      openswitch: false,
      pass: false,
      ruleValidate: {
        computer_room: [{
          required: true,
          message: '',
          trigger: 'change'
        }],
        connection_name: [{
          required: true,
          message: '',
          trigger: 'change'
        }],
        basename: [{
          required: true,
          message: '',
          trigger: 'change'
        }],
        tablename: [{
          required: true,
          message: '',
          trigger: 'change'
        }],
        text: [{
            required: true,
            message: '',
            trigger: 'change'
          },
          {
            type: 'string',
            max: 20,
            message: '20',
            trigger: 'blur'
          }
        ]
      },
      formItem: {
        text: '',
        computer_room: '',
        connection_name: '',
        basename: '',
        tablename: '',
        backup: '0',
        assigned: ''
      },
      id: null,
      tabs: 'order1',
      optionData: [
        'varchar',
        'int',
        'char',
        'tinytext',
        'text',
        'mediumtext',
        'longtext',
        'blob',
        'mediumblob',
        'longblob',
        'tinyint',
        'smallint',
        'mediumint',
        'bigint',
        'time',
        'year',
        'date',
        'datetime',
        'timestamp',
        'decimal',
        'float',
        'double',
        'jason'
      ],
      assigned: [],
      formDynamic: '',
      validate_gen: true
    }
  },
  methods: {
    editorInit: function () {
      require('brace/mode/mysql')
      require('brace/theme/xcode')
    },
    Connection_Name (index) {
      if (index) {
        this.ScreenConnection(index)
      }
    },
    test_sql () {
      let ddl = ['select', 'insert', 'update', 'delete']
      let createtable = this.formDynamic.split(';')
      for (let i of createtable) {
        for (let c of ddl) {
          i = i.replace(/(^\s*)|(\s*$)/g, '')
          if (i.toLowerCase().indexOf(c) === 0) {
            this.$Message.error('DDL!');
            return false
          }
        }
      }
      this.$refs['formItem'].validate((valid) => {
        if (valid) {
            let tmp = this.formDynamic.replace(/(;|)$/gi, '').replace(//g, ';')
            axios.put(`${util.url}/sqlsyntax/test`, {
              'id': this.id[0].id,
              'base': this.formItem.basename,
              'sql': tmp
            })
              .then(res => {
                if (res.data.status === 200) {
                  this.Testresults = res.data.result
                  let gen = 0
                  this.Testresults.forEach(vl => {
                    if (vl.errlevel !== 0) {
                      gen += 1
                    }
                  })
                  if (gen === 0) {
                    this.validate_gen = false
                  } else {
                    this.validate_gen = true
                  }
                } else {
                  this.$Notice.error({
                    title: '',
                    desc: 'Inception!'
                  })
                }
              })
              .catch(error => {
                util.ajanxerrorcode(this, error)
              })
          } else {
            this.$Message.error('sql!');
          }
      })
    },
    handleSubmit () {
      let createtable = this.formDynamic.replace(/(;|)$/gi, '').replace(/\s/g, ' ').replace(//g, ';').split(';')
      this.validate_gen = true
      for (let i of createtable) {
        this.sql.push(i)
      }
    },
    DataBaseName (index) {
      if (index) {
        this.id = this.item.filter(item => {
          if (item.connection_name === index) {
            return item
          }
        })
        axios.put(`${util.url}/workorder/basename`, {
            'id': this.id[0].id
          })
          .then(res => {
            this.tableform.basename = res.data
          })
          .catch(() => {
            this.$Notice.error({
              title: '',
              desc: '!'
            })
          })
      }
    },
    ScreenConnection (b) {
      this.tableform.sqlname = this.item.filter(item => {
        if (item.computer_room === b) {
          return item
        }
      })
    },
    GetTableName () {
      if (this.formItem.basename) {
        let data = JSON.stringify(this.formItem)
        axios.put(`${util.url}/workorder/tablename`, {
            'data': data,
            'id': this.id[0].id
          })
          .then(res => {
            this.tableform.info = res.data
          }).catch(error => {
            util.ajanxerrorcode(this, error)
          })
      }
    },
    getdatabases () {
      axios.put(`${util.url}/workorder/connection`, {'permissions_type': 'ddl'})
        .then(res => {
          this.item = res.data['connection']
          this.assigned = res.data['assigend']
        })
        .catch(error => {
          util.ajanxerrorcode(this, error)
        })
    },
    getinfo () {
      this.$refs['formItem'].validate((valid) => {
        if (valid) {
          this.$Spin.show({
            render: (h) => {
              return h('div', [
                h('Icon', {
                  'class': 'demo-spin-icon-load',
                  props: {
                    type: 'load-c',
                    size: 30
                  }
                }),
                h('div', ',........')
              ])
            }
          })
          this.formItem.table_name = this.formItem.tablename
          axios.put(`${util.url}/workorder/field`, {
              'connection_info': JSON.stringify(this.formItem),
              'id': this.id[0].id
            })
            .then(res => {
              this.TableDataOld = res.data
              this.TableDataNew = Array.from(this.TableDataOld)
              this.$Spin.hide()
            })
            .catch(() => {
              this.$Notice.error({
                title: '',
                desc: ''
              })
            })
          this.getindex()
        } else {
          this.$Message.error('!');
        }
      })
    },
    AddColumns () {
      if (this.Add_tmp.Field === '' || this.Add_tmp.Null === null || this.Add_tmp.Species === '') {
        this.$Notice.warning({
          title: ','
        })
      } else {
        if (this.Add_tmp.Extra) {
          this.Add_tmp.Extra = this.Add_tmp.Extra.replace(/\s+/g, '')
        }
        if (this.Add_tmp.Len !== '') {
          this.Add_tmp.Type = `${this.Add_tmp.Species}(${this.Add_tmp.Len})`
        } else {
          this.Add_tmp.Type = `${this.Add_tmp.Species}`
        }
        this.add_row.push(JSON.parse(JSON.stringify(this.Add_tmp)))
        for (let c of Object.keys(this.Add_tmp)) {
          this.Add_tmp[c] = ''
          this.Add_tmp.Default = null
          this.Add_tmp.Extra = null
        }
      }
    },
    ClearColumns () {
      this.Add_tmp = {}
    },
    remove (index) {
      this.putdata.push({
        'del': index,
        'table_name': this.formItem.tablename
      })
    },
    canel () {
      this.sql = []
      this.pass = false
    },
    edit_tab (col) {
      this.TableDataNew[col.index] = col.row
      this.$Notice.success({
        title: `${col.row.Field}-!`
      })
    },
    confirmsql () {
      if (this.Add_tmp.Field !== '') {
        this.$Notice.warning({
          title: '',
          desc: '!'
        })
      } else {
        this.TableDataNew.forEach((item, i) => {
          if (this.TableDataNew[i].Type === this.TableDataOld[i].Type &&
            this.TableDataNew[i].Field === this.TableDataOld[i].Field &&
            this.TableDataNew[i].Default === this.TableDataOld[i].Default &&
            this.TableDataNew[i].Extra === this.TableDataOld[i].Extra &&
            this.TableDataNew[i].Null === this.TableDataOld[i].Null) {} else {
            this.putdata.push({
              'edit': this.TableDataNew[i],
              'table_name': this.formItem.tablename
            })
          }
        })
        this.putdata.push({
          'add': this.add_row,
          'table_name': this.formItem.tablename
        })
        axios.put(`${util.url}/gensql/sql`, {
            'data': JSON.stringify(this.putdata),
            'basename': this.formItem.basename
          })
          .then(res => {
            for (let i of res.data) {
              this.sql.push(i)
            }
            this.putdata = []
            this.add_row = []
            this.TableDataNew = Array.from(this.TableDataOld)
          }).catch(error => {
            util.ajanxerrorcode(this, error)
          })
      }
    },
    orderswitch () {
      this.openswitch = !this.openswitch
    },
    commitorder () {
      if (this.sql === [] || this.formItem.basename === '' || this.assigned === '' || this.formItem.text === '' || this.formItem.assigned === '') {
        this.$Notice.error({
          title: '',
          desc: ',!'
        })
      } else {
        if (this.pass === true) {
          axios.post(`${util.url}/sqlsyntax/`, {
            'data': JSON.stringify(this.formItem),
            'sql': JSON.stringify(this.sql),
            'user': Cookies.get('user'),
            'type': 0,
            'id': this.id[0].id
          })
            .then(res => {
              this.$Notice.success({
                title: '',
                desc: res.data
              })
              this.$router.push({
                name: 'myorder'
              })
            }).catch(error => {
            util.ajanxerrorcode(this, error)
          })
        } else {
          this.$Notice.warning({
            title: '',
            desc: ''
          })
        }
      }
    }
  },
  mounted () {
    this.getdatabases()
  }
}
</script>



